import { __ } from '@wordpress/i18n';
import { useEffect, useState, useRef } from '@wordpress/element';
import { MediaUpload, MediaUploadCheck } from '@wordpress/block-editor';
import { PanelRow, Button, Placeholder } from '@wordpress/components';

import Settings from './settings';

const Edit = props => {
    const { className, attributes: { source, poster, controls, width, radius, repeat, muted, autoHideControl }, setAttributes, clientId } = props;

    useEffect(() => { clientId && setAttributes({ clientId }); }, [clientId]); // Set & Update clientId to clientId

    const [mediaSource, setMediaSource] = useState();
    const videoWrapper = useRef(null);
    const videoElement = useRef(null);

    // Controls handler
    const controlsHandler = (controls) => {
        const newControls = [];
        Object.keys(controls).map(item => {
            if (controls[item]) {
                newControls.push(item);
            }
        });
        return newControls;
    };

    useEffect(() => {
        if (source) {
            videoWrapper.current.innerHTML = '';
            videoWrapper.current.innerHTML = videoElement.current.outerHTML;

            new Plyr(videoWrapper.current.children[0], {
                controls: controlsHandler(controls),
                clickToPlay: false,
                loop: { active: repeat },
                muted,
                hideControls: autoHideControl,
            });
        }
    }, [source, poster, controls, videoWrapper]);

    const handleApply = (event) => {
        event.preventDefault();
        setAttributes({ source: mediaSource });
        setMediaSource('');
    };

    return source ? <>
        <Settings attributes={props.attributes} setAttributes={setAttributes} clientId={clientId} />

        <div className={className} id={`vpbVideoPlayer-${clientId}`}>
            <style dangerouslySetInnerHTML={{
                __html: `
                    #vpbVideoPlayer-${clientId} .vpbVideoPlayer{
                        width: ${'0px' === width || '0%' === width || '0em' === width ? '100%' : width};
                        border-radius: ${radius};
                    }
                `}} />

            <div className='vpbVideoPlayer'>
                <div className='videoWrapper' ref={videoWrapper}></div>

                <video controls playsinline data-poster={poster} preload={'metadata'} ref={videoElement}>
                    Your browser does not support the video tag.
                    <source src={source} type='video/mp4' />
                </video>
            </div>
        </div>
    </> : <Placeholder label={__('Upload a Video ', 'video-player')} className='vpbVideoPlaceholder'
        instructions={__('Upload a video or paste/write video url to get started.', 'video-player')}
        icon={<svg xmlns='http://www.w3.org/2000/svg' width={24} height={24} viewBox='0 0 24 24' className='html5-block-icon' fill='none' stroke='currentColor' strokeWidth='2' strokeLinecap='round' strokeLinejoin='round'>
            <polygon points='23 7 16 12 23 17 23 7'></polygon>
            <rect x='1' y='5' width='15' height='14' rx='2' ry='2'></rect>
        </svg>}
    >

        <MediaUploadCheck>
            <MediaUpload
                allowedTypes={['video']}
                onSelect={(source) => setAttributes({ source: source.url })}
                render={({ open }) => <Button isPrimary onClick={open}> {__('Upload', 'video-player')} </Button>}
            />
        </MediaUploadCheck>

        <PanelRow className='vpbUrlInput'>
            <h3> Or </h3>
            <input
                type='url'
                aria-label={__('URL', 'video-player')}
                placeholder={__('Paste or type a video URL', 'video-player')}
                onChange={(src) => setMediaSource(src.target.value)}
                value={mediaSource}
            />
            <Button label={__('Apply', 'video-player')} type='submit' onClick={handleApply} isPrimary>Apply</Button>
        </PanelRow>
    </Placeholder>;
};
export default Edit;